







































































{ Query } = require "~plugins/memory-record"

module.exports =
  mixins: [
    require '~plugins/book'
  ]

  data: ->
    sort: "live"
    order: "asc"
    full_mode: true
    live_mode: true

  computed:
    full_on:  ->  @potof_ids -> false
    full_off: ->  @potof_ids -> true
    live_on:  ->  @potof_ids (o)-> ! o.commit
    live_off: ->  @potof_ids (o)-> o.commit

    potofs: ->
      hides = @hide_potof_ids
      if @part
        { list } = Query.potofs.catalog(@book_id, @part_id, @sort, @order)
        for o in list
          o.hide = false
        for id in hides
          Query.potofs.find(id).hide = true
        list
      else
        []

    bgc: ->
      switch @sort
        when "text", "role_labels", "winner_id", "win"
          (o)-> o.winner_id
        else
          (o)-> o.live.role_id

    show: ->
      @$store.state.menu.set.potof && @part

  methods:
    potof_ids: (f)->
      @potofs
      .filter f
      .map (o)-> o.id
      .sort()

    toggle: (o)->
      o.hide = ! o.hide
      @hide_potof_ids = @potof_ids (o)-> o.hide

    reverse: ->
      switch @order
        when "asc"
          @order = "desc"
        when "desc"
          @order = "asc"

    count: (unit, n)->
      switch n
        when 0, undefined, null, Infinity
          " "
        else
          "#{n}#{unit}"




