
{ Query } = require "~plugins/memory-record"

module.exports =
  props:
    img_src:
      type: String
    face_id:
      type: String
    hide:
      type: Boolean
  computed:
    has_html: ->
      !! @$slots.default
    image_class: ->
      if @hide
        ["hide"]
      else
        []
    image_src: ->
      face = Query.faces.find(@face_id, "all")
      switch
        when @img_src
          @img_src
        when face
          "#{env.STORE_URL}/images/portrate/#{ face.id }.jpg"

  methods:
    click: ->
      @$emit 'click', @face_id





