


module.exports =
  mixins: [
    require("~plugins/browser-store")
      cookie:
        theme: "cinema"
        font:  "std"
      watch: (val, key)->
        return unless window?
        @use[key]?.unuse()
        @use[key] = require "~assets/styl/#{key}-#{val}.styl.use"
        @use[key].use()
  ]
  data: ->
    top:    0
    width:  0
    height: 0
    use: {}
    welcome: true

  created: ->
    return unless window?
    document.ontouchstart = ->
    @poll()

  computed:
    center: ->
      @$store.commit "menu/center", { @top, @left, @height, @width }
    body_class: ->
      [@theme, @font].join("~")

  methods:
    poll: ->
      if @top == scrollY && @left == scrollX
        @center
      @top = scrollY
      @left = scrollX
      @width = innerWidth
      @height = innerHeight
      requestAnimationFrame @poll




